@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,300;1,400&display=swap");
body {
  font-family: "Poppins", sans-serif !important;
}

.hero-img {
  width: 100%;
  height: 50vh;
  display: grid;
  place-content: center;
  background: url("./images/hero.jpg") bottom no-repeat fixed !important;
  background-size: cover;
  position: relative;
  z-index: 0;
}

.hero-img::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, transparent, black);
  z-index: -2;
}

.custom-clr1 {
  color: #828385 !important;
}

.custom-clr2 {
  color: #ffc107 !important;
}

.btn-search {
  border: 2px solid #ffc107 !important;
}

.program-img {
  cursor: pointer;
}
.program-img img {
  transition: all 200ms ease-in-out;
}
.program-img:hover img {
  transform: scale(1.05);
}